@use 'partials/variables' as var;
@use 'header';
@use 'footer';
@import url('https://fonts.googleapis.com/css2?family=Lora:ital,wght@0,400..700;1,400..700&family=Nunito+Sans:ital,opsz,wght@0,6..12,200..1000;1,6..12,200..1000&display=swap');

*{
    box-sizing: border-box;
    margin: 0;
    padding: 0;
}

:root{
    font-family: var.$nunito;
    font-size: 16px;
    scroll-behavior: smooth;
    scroll-padding-top: 9rem;
}

body{
    display: flex;
    flex-direction: column;
    position: relative;

    main{
        background-color: var.$color-3;
        box-shadow: inset 0 0 6px 4px rgba(0, 0, 0, .5);
        display: flex;
        flex-direction: column;
        justify-content: center;
        padding: 4rem 2rem;

        @media screen and #{var.$medium-up} {
            padding: 4rem;
        }

        .btn__retour-haut{
            background-color: var.$color-2;
            border: .2rem solid var.$color-5;
            border-radius: 50%;
            bottom: 3rem;
            box-shadow: 0 0 8px 4px rgba(0, 0, 0, 20%);
            cursor: pointer;
            height: 60px;
            padding: .95rem;
            position: fixed;
            right: 2rem;
            width: 60px;
            z-index: 1;
        }

        .btn__retour-haut:hover{
            background-color: var.$color-5;
            border: .2rem solid var.$color-5;
        }
        
        .btn__retour-haut:active{
            box-shadow: inset 0 0 8px 4px rgba(0, 0, 0, 20%);
        }

        .css3-list__main-container{
            display: flex;
            flex-direction: column;
            gap: 2rem;
            margin: 0 auto;
            max-width: 1024px;
            width: 100%;

            .main-list__item{
                border: .2rem solid var.$color-1;
                border-radius: .3rem;
                box-shadow: 0 0 6px 2px rgb(0, 0, 0, .3);

                h2{
                    background-color: var.$color-1;
                    color: var.$color-2;
                    font-family: "Lora", serif;
                    font-size: 1.6rem;
                    padding: 1rem;
                    text-align: center;
                    user-select: none;

                    @media screen and #{var.$large-up} {
                        font-size: 2rem;
                    }

                    @media screen and #{var.$x-large-up} {
                        font-size: 2.2rem;
                    }
                }

                .properties__container{
                    border-radius: 0 0 .3rem .3rem;
                    display: flex;
                    flex-direction: column;

                    h3{
                        background-color: var.$color-5;
                        border-bottom: .1rem solid var.$color-1;
                        color: var(--couleur-un);
                        display: flex;
                        flex-direction: row;
                        font-size: 1.4rem;
                        font-weight: 400;
                        filter: brightness(1.1);
                        justify-content: space-between;
                        padding: 1rem 2rem;
                        position: relative;

                        @media screen and #{var.$large-up} {
                            font-size: 1.8rem;
                            background-position: bottom .9rem right 2rem;
                        }

                        @media screen and #{var.$x-large-up} {
                            background-position: bottom 1.1rem right 2rem;
                            font-size: 2.2rem;
                        }
            
                        img{
                            transition: transform .4s;
                            width: 2rem;
                        
                            .open-arrow{
                                transform: rotate(180deg);
                            }
                        }
                    }

                    :last-child h3{
                        border-radius: 0 0 .1rem .1rem;
                        border-bottom: none;
                    }
            
                    h3:hover{
                        cursor: pointer;
                        filter: brightness(1);
                    }
            
                    .dropdown{
                        background-color: var.$color-2;
                        box-shadow: inset 0 0 6px 2px rgba(0, 0, 0, .2);
                        display: flex;
                        flex-direction: column;
                        gap: 3rem;
                        padding: 2rem 2rem 6rem 2rem;
            
                        h4{
                            font-size: 1.2rem;
                            padding-bottom: 1rem;

                            @media screen and #{var.$large-up} {
                                font-size: 1.4rem;
                            }

                            @media screen and #{var.$x-large-up} {
                                font-size: 1.8rem;
                            }
                        }
            
                        p{
                            color: var.$color-1;
                            font-size: .9rem;
                            line-height: 1.5;
                            padding-bottom: 1rem;
                        }
            
                        textarea{
                            box-shadow: 2px 2px 2px 0 rgb(0 0 0 / 40%);
                            border: none;
                            border-radius: 2rem;
                            height: 300px;
                            padding: 2rem;
                            width: 100%;

                            @media screen and #{var.$medium-up} {
                                width: 85%;
                            }

                            @media screen and #{var.$large-up} {
                                width: 75%;
                            }

                            @media screen and #{var.$x-large-up} {
                                width: 60%;
                            }
                        }

                        ul{
                            display: flex;
                            flex-direction: column;
                            gap: .5rem;
                        }
                    }

                    .animation__container{
                        section.animation__exemple{
                            height: 380px;
                            position: relative;
                        }
                        
                        div.animation__exemple{
                            align-items: center;
                            animation-name: animation__exemple;
                            animation-duration: 3s;
                            animation-iteration-count: infinite;
                            border-radius: .5rem;
                            display: flex;
                            font-size: .8rem;
                            justify-content: center;
                            position: absolute;
                            height: 100px;
                            width: 100px;

                            @media screen and #{var.$medium-up} {
                                height: 150px;
                                width: 150px;
                            }
                        }
                        
                        @keyframes animation__exemple {
                            0%   {background-color:var(--couleur-un); left:0px; top:0px;color: var(--couleur-deux);}
                            25%  {background-color:var(--couleur-quatre); left:140px; top:0px;color: var(--couleur-un);}
                            50%  {background-color:var(--couleur-cinq); left:140px; top:240px;color: var(--couleur-un);}
                            75%  {background-color:var(--couleur-trois); left:0px; top:240px;color: var(--couleur-un);}
                            100% {background-color:var(--couleur-un); left:0px; top:0px;color: var(--couleur-deux);}
                        }
                    }

                    .backface-visibility__container{
                        div.backface-visibility{
                            position: relative;
                        }

                        div.backface-visibility-exemple{
                            display: flex;
                            flex-wrap: wrap;
                            gap: 2rem;
                            height: 360px;
                        
                            @media screen and #{var.$small-up}{
                                height: 180px;
                            }
                        }
                        
                        .frontface-1, .frontface-2{
                            background-color: var.$color-5;
                            border-radius: .3rem;
                            padding: 2rem;
                        }
                        
                        .backface{
                            background-color: var.$color-4;
                            border-radius: .3rem;
                            padding: 2rem;
                        }
                        
                        .frontface-1{
                            opacity: .7;
                            position: absolute;
                            z-index: 1;
                        }
                        
                        .backface.__visible{
                            backface-visibility: visible;
                            position: absolute;
                            left: 1rem;
                            transform: rotateY(180deg);
                        }
                        
                        .frontface-2{
                            opacity: .7;
                            position: absolute;
                            z-index: 1;
                        }
                        
                        .backface.__hidden{
                            backface-visibility: hidden;
                            position: absolute;
                            left: 1rem;
                            transform: rotateY(180deg);
                        }
                    }

                    .border-radius__container{
                        div.border-radius-exemple{
                            display: flex;
                            flex-wrap: wrap;
                            gap: 3rem;
                            justify-content: center;
                        }
                        
                        div.border-radius{
                            background-color: var.$color-5;
                            display: flex;
                            flex-wrap: wrap;
                            font-size: 1rem;
                            justify-content: center;
                            padding: 1.5rem;
                            width: 100px;
                        }
                        
                        div.border-radius.exemple-1{
                            border-radius: 1rem;
                        }
                        
                        div.border-radius.exemple-2{
                            border-radius: 2rem 4rem 6rem 0rem;
                        }
                    }

                    .border-image__container{
                        h4.border-image{
                            border: 2rem solid transparent;
                            border-image: url("https://cdn.pixabay.com/photo/2020/02/06/01/52/frame-4822807_1280.png") 100 round;
                            display: flex;
                            justify-content: center;
                            padding: 1rem;
                        }
                    }

                    .columns__container{
                        .columns__exemple{
                            columns: 2;
                        }
                        
                        .columns__exemple h4{
                            column-span: all;
                            text-align: center;
                        }
                    }

                    .filter__container{
                        .filter__exemples{
                            display: flex;
                            flex-wrap: wrap;
                            gap: 1.5rem;
                            justify-content: space-evenly;
                        }
                        
                        figcaption{
                            font-size: 1.2rem;
                            padding: 1rem;
                            text-align: center;
                        }
                        
                        .filter__exemples img{
                            border-radius: 1rem;
                            box-shadow: 4px 4px 6px 0 rgb(0 0 0 / 50%);
                        }
                        
                        .filter-1{
                            filter: brightness(.5);
                        }
                        
                        .filter-2{
                            filter: contrast(1.5);
                        }
                        
                        .filter-3{
                            filter: grayscale(1);
                        }
                        
                        .filter-4 img{
                            filter: invert(1);
                        }
                        
                        .filter-5{
                            filter: saturate(3);
                        }
                    }

                    .gradients__container{
                        section.gradients{
                            display: flex;
                            flex-direction: column;
                            gap: 2rem;
                            padding-bottom: 6rem;
                        }
                        
                        h4.gradients{
                            font-size: 1.4rem;
                        }
                        
                        div.linear-gradient{
                            background: linear-gradient(var.$color-3, var.$color-5);
                            height: 200px;
                            width: 100%;
                        }
                        
                        div.radial-gradient{
                            background: radial-gradient(var.$color-3, var.$color-5);
                            height: 200px;
                            width: 100%;
                        }
                    }

                    .grid-layout__container{
                        div.flexbox-exemple{
                            display: flex;
                            flex-wrap: wrap;
                            gap: 1rem;
                            justify-content: space-evenly;
                        }
                        
                        .square, .square{
                            align-items: center;
                            background-color: var.$color-5;
                            border-radius: .2rem;
                            display: flex;
                            font-size: 1.4rem;
                            font-weight: 600;
                            height: 140px;
                            justify-content: center;
                            width: 140px;
                        }
                        
                        .grid-layout{
                            display: grid;
                            gap: 1rem;
                            grid-template-columns: repeat(2, auto);

                            @media screen and #{var.$medium-up} {
                                grid-template-columns: repeat(3, auto);
                            }

                            @media screen and #{var.$large-up} {
                                grid-template-columns: repeat(4, auto);
                            }
                        }
                    }

                    .object-fit__container{
                        div.object-fit-exemple{
                            align-items: center;
                            display: flex;
                            flex-wrap: wrap;
                            gap: 3rem;
                            justify-content: center;
                        }
                        
                        .object-fit img{
                            height: 200px;
                            width: 300px;
                        }
                        
                        section.object-fit-exemple{
                            height: fit-content;
                        }
                        
                        .fill img{
                            object-fit: fill;
                        }
                        
                        .contain img{
                            object-fit: contain;
                        }
                        
                        .cover img{
                            object-fit: cover;
                        }
                        
                        .none img{
                            object-fit: none;
                        }
                        
                        .scale-down img{
                            object-fit: scale-down;
                        }
                    }

                    .opacity__container{
                        div.opacity-exemple{
                            align-items: center;
                            display: flex;
                            flex-wrap: wrap;
                            gap: 2rem;
                            justify-content: center;
                        }
                        
                        .opacity-10 img{
                            opacity: .1;
                        }
                        
                        .opacity-25 img{
                            opacity: .25;
                        }
                        
                        .opacity-50 img{
                            opacity: .5;
                        }
                        
                        .opacity-75 img{
                            opacity: .75;
                        }
                    }

                    .outline__offset{
                        div.outline-offset-exemple{
                            display: flex;
                            flex-direction: column;
                            gap: 4rem;
                        }
                        
                        .outline-offset{
                            background-color: var.$color-5;
                            display: flex;
                            justify-content: center;
                            padding: 1rem;
                        }
                        
                        .outline-offset._1{
                            border: .2rem solid var.$color-4;
                            outline: .2rem solid var.$color-1;
                            outline-offset: .5rem;
                        }
                        
                        .outline-offset._2{
                            border: .2rem solid var.$color-4;
                            outline: .2rem dashed var.$color-1;
                            outline-offset: 1rem;
                        }
                    }

                    .overflow__container{
                        div.overflow-exemple{
                            align-items: center;
                            display: flex;
                            flex-direction: column;
                            gap: 6rem;
                            justify-content: center;
                        }
                        
                        div.overflow-exemple div{
                            align-items: center;
                            display: flex;
                            flex-direction: column;
                            gap: .5rem;
                        }
                        
                        div.overflow-exemple p{
                            border: .2rem solid var.$color-1;
                            border-radius: .2rem;
                            height: 100px;
                            padding: .5rem;
                            width: 200px;
                        }
                        
                        .overflow-hidden{
                            overflow: hidden;
                        }
                        
                        .overflow-scroll{
                            overflow: scroll;
                        }
                        
                        .overflow-visible{
                            overflow: visible;
                        }
                    }

                    .transform__container{

                        div.transform-exemple{
                            align-items: center;
                            display: flex;
                            flex-direction: column;
                            gap: 8rem;
                            justify-content: center;
                        }
                        
                        div.transform {
                            align-items: center;
                            background-color: var.$color-5;
                            display: flex;
                            flex-wrap: wrap;
                            font-size: 1rem;
                            height: 100px;
                            justify-content: center;
                            padding: .5rem;
                            width: 200px;
                        }
                        
                        .transform.none{
                            transform: none;
                        }
                        
                        .transform.rotate-1{
                            transform: rotate(45deg);
                        }
                        
                        .transform.rotate-2{
                            transform: rotate(90deg);
                        }
                        
                        .transform.scale{
                            transform: scale(2.1);
                        }
                        
                        .transform.skew{
                            transform: skew(30deg, 10deg);
                        }
                        
                        .transform.translate{
                            transform: translate(100px, -50px);
                        }
                    }

                    .transition__container{
                        div.transition-exemple{
                            align-items: flex-start;
                            display: flex;
                            flex-direction: column;
                            gap: 2rem;
                        }   
                        
                        div.transition{
                            background-color: var.$color-5;
                            border-radius: .5rem;
                            padding: 1.5rem;
                            width: 220px;
                        }
                        
                        div.transition p{
                            font-size: .9rem;
                            width: 180px;
                        }
                        
                        div.transition.exemple-1{
                            transition: width 1s;
                        }
                        
                        div.transition.exemple-2{
                            transition: width 3s;
                        }
                        
                        div.transition.exemple-3{
                            transition: width 6s;
                        }
                        
                        div.transition.exemple-1:hover, div.transition.exemple-2:hover, div.transition.exemple-3:hover{
                            width: 100%;
                        }
                    }

                    .writing-mode__container{
                            
                        div.writing-mode-exemple{
                            align-items: flex-start;
                            display: flex;
                            flex-direction: column;
                            gap: 2rem;
                            justify-content: center;
                        }
                        
                        .writing-mode{
                            background-color: var.$color-5;
                            border-radius: .5rem;
                            padding: 2rem;
                            width: 60%;
                        }
                        
                        .writing-mode.horizontal-tb{
                            writing-mode: horizontal-tb;
                        }
                        
                        .writing-mode.vertical-lr{
                            writing-mode: vertical-lr;
                        }
                        
                        .writing-mode.vertical-rl{
                            writing-mode: vertical-rl;
                        }
                    }
                }
            } 
        }
    }

    ul{
        list-style: none;
    }

    p{
        @media screen and #{var.$large-up} {
            font-size: 1rem;
        }

        @media screen and #{var.$x-large-up} {
            font-size: 1.3rem;
        }
    }

    .hidden{
        display: none !important;
    }   
}

